<template>
  <div class="block">
    <el-row>
      <el-col :xs="24">
        <div class="bg-green-light header">25x.no</div>
      </el-col>
    </el-row>

    <el-row>
      <el-col :sm="6">
        <br>
      </el-col>
      <el-col :sm="12">
        <div class="grid-content intro">
          25x er et begrep fra personlig økonomi. 25 ganger årlig forbruk er typisk den summen du må spare opp for å kunne pensjonere deg og leve av avkastningen/rentene fra investeringene dine i evig tid.<br>
          <br>
          Dette gjør at du kan ta ut 4% årlig uten at det historisk skal gå ut over fremtidig formue og avkastning. Det er omtrent som Oljefondets handlingsregel, for den egen økonomi.<br>
          <br>
          Hvorfor akkurat 25? Historiske simuleringer hundre år tilbake i tid viser at 4% avkastning etter inflasjon er en realistisk forventning om avkastning i aksjemarkedet over lang tid. (Ref: <a href="https://en.wikipedia.org/wiki/Trinity_study">Wikipedia: Trinity Study</a>, <a href="http://firecalc.com/">FireCalc.com</a>, <a href="http://www.retailinvestor.org/pdf/Bengen1.pdf">Determining withdrawal rates using historical data</a>)
        </div>
      </el-col>
      <el-col :sm="6">
        <br>
      </el-col>
    </el-row>

    <el-row>
      <el-col :xs="24">
        <div class="bg-green-light years">Du er økonomisk uavhengig om <strong>{{ yearsTo25 | timeFormat }}</strong> år!</div>
      </el-col>
    </el-row>

    <div class="">

      <el-row>
        <el-col :xs="24">
          <div class="values">Innstillinger</div>
        </el-col>
      </el-row>

      <el-row>
        <el-col :lg="4">
          <div class="grid-content"><br></div>
        </el-col>
        <el-col :xs="24" :sm="6" :lg="4">
          <div class="grid-content">Forbruk: </div>
        </el-col>
        <el-col :xs="24" :sm="12" :lg="8">
          <div class="grid-content">
            <el-slider v-model="consumptionPerMonth" :min=1000 :max=salaryPerMonth :step=1000></el-slider>
            25x årlig forbruk:  <strong>{{ twentyFiveTotal | moneyFormat }}</strong>
        </div>
        </el-col>
        <el-col :xs="24" :sm="6" :lg="4">
          <div class="grid-content">Måned: {{ consumptionPerMonth | moneyFormat }} <br> År: {{ consumptionPerMonth | multiply12 | moneyFormat }}</div>
        </el-col>
        <el-col :lg="4">
          <div class="grid-content"><br></div>
        </el-col>
      </el-row>
      <hr>
      <el-row>
        <el-col :lg="4">
          <div class="grid-content"><br></div>
        </el-col>
        <el-col :xs="24" :sm="6" :lg="4">
          <div class="grid-content">Utbetalt lønn: </div>
        </el-col>
        <el-col :xs="24" :sm="12" :lg="8">
          <div class="grid-content">
            <el-slider v-model="salaryPerMonth" :min=1000 :max=100000 :step=1000></el-slider>
            Sparing: {{ savingsPerMonth | moneyFormat }} måned / <strong>{{ savingsPerMonth | multiply12 | moneyFormat }} år</strong>
          </div>
        </el-col>
        <el-col :xs="24" :sm="6" :lg="4">
          <div class="grid-content">Måned: {{ salaryPerMonth | moneyFormat }} <br> År: {{ salaryPerMonth | multiply12 | moneyFormat }}</div>
        </el-col>
        <el-col :lg="4">
          <div class="grid-content"><br></div>
        </el-col>
      </el-row>
      <hr>
      <el-row>
        <el-col :lg="4">
          <div class="grid-content"><br> </div>
        </el-col>
        <el-col :xs="24" :sm="6" :lg="4">
          <div class="grid-content">Penger investert i dag:  </div>
        </el-col>
        <el-col :xs="24" :sm="12" :lg="8">
          <div class="grid-content">
            <el-slider v-model="currentSavings" :max=twentyFiveTotal :step=10000></el-slider>
            Mangler for å få 25x:  <strong>{{ twentyFiveMissing | moneyFormat }}</strong>
          </div>
        </el-col>
        <el-col :xs="24" :sm="6" :lg="4">
          <div class="grid-content">{{ currentSavings | moneyFormat }}</div>
        </el-col>
        <el-col :lg="4">
          <div class="grid-content"><br></div>
        </el-col>
      </el-row>
    </div>

    <el-row>
      <el-col :xs="24">
        <div class="bg-green-light footer-text">

            Made with
            <svg class="heart" viewBox="0 0 32 29.6">
              <path d="M23.6,0c-3.4,0-6.3,2.7-7.6,5.6C14.7,2.7,11.8,0,8.4,0C3.8,0,0,3.8,0,8.4c0,9.4,9.5,11.9,16,21.2
            	c6.1-9.3,16-12.1,16-21.2C32,3.8,28.2,0,23.6,0z"/>
            </svg>
            by <a href="https://www.datadynamics.no/">DataDynamics</a>.

        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import wNumb from 'wnumb'

const moneyFormat = wNumb({
  'mark': ',',
  'thousand': '.',
  'prefix': '',
  'postfix': ' kr',
  'decimals': 0
})

const timeFormat = wNumb({
  'mark': ',',
  'thousand': '.',
  'prefix': '',
  'postfix': '',
  'decimals': 2
})

export default {
  'name': 'twentyFiveX',
  data () {
    return {
      'currentSavings': 100000,
      'salaryPerMonth': 25000,
      'consumptionPerMonth': 15000
    }
  },
  'computed': {
    'savingsPerMonth': function () {
      return this.salaryPerMonth - this.consumptionPerMonth
    },
    'twentyFiveTotal': function () {
      return this.consumptionPerMonth * 12 * 25
    },
    'twentyFiveMissing': function () {
      return this.twentyFiveTotal - this.currentSavings
    },
    'yearsTo25': function () {
      return this.twentyFiveMissing / (this.savingsPerMonth * 12)
    }
  },
  'filters': {
    'moneyFormat': function (value) {
      return moneyFormat.to(value)
    },
    'timeFormat': function (value) {
      return timeFormat.to(value)
    },
    'multiply12': function (value) {
      return value * 12
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-row {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  .el-col {
    border-radius: 4px;
  }
  .bg-green-dark {
    background: #009444;
  }
  .bg-green-light {
    background: #8dc63f;
  }
  .bg-blue {
    background: #5cb2e4;
  }
  .bg-gray {
    background: #dddede;
  }

  .header{
    font-family: 'Lato', sans-serif;
    color: white;
    font-size: 7em;
    font-weight: 900;
    margin: 0px 0px 0px 0px;
    padding: 30px 30px 30px 30px;
  }

  .intro{
    font-size: 1em;
  }

  .years{
    font-family: 'Lato', sans-serif;
    font-size: 3em;
    font-weight: 700;
    color: white;
    padding: 60px 60px 60px 60px;
  }

  .values{
    padding: 10px 10px 10px 10px;
    font-size: 1.5em;
  }

  .grid-content {
    padding: 10px 10px 10px 10px;
  }
  .row-min-height{
    min-height: 60px;
  }

  hr{
    margin: 20px 20px 20px 20px;
    border-top: 1px dotted #8c8b8b;
    border-bottom: 1px dotted #fff;
  }

  .footer-text{
    font-family: 'Lato', sans-serif;
    font-size: 1em;
    font-weight: 300;
    color: white;
    padding: 20px 20px 20px 20px;
    margin-bottom: 0px;
  }

  .footer{
    position: absolute;
    bottom: 0;
  }

  .heart {
    fill: red;
    position: relative;
    top: 5px;
    width: 20px;
    animation: pulse 1s ease infinite,
  }
</style>



