import Vue from 'vue'
import Router from 'vue-router'
import module25x from '@/components/module25x'

Vue.use(Router)

export default new Router({
  'routes': [
    {
      'path': '/',
      'name': 'module25x',
      'component': module25x
    }
  ]
})



